	module.exports = {"data":{"markdownRemark":{"html":"<h1>July 6, 2016</h1>\n<ul>\n<li>Matt Farina gave an introduction and overview of the agenda</li>\n<li>\n<p>Chris Love gave a demo of his Cassandra cluster</p>\n<ul>\n<li>It was a great example of PetSet in action</li>\n<li>His demo application is located at <a href=\"https://github.com/k8s-for-greeks/gpmr\">https://github.com/k8s-for-greeks/gpmr</a></li>\n</ul>\n</li>\n<li>\n<p>Matt Farina gave an update on the analysis of the sig-apps survey</p>\n<ul>\n<li>He will reach out to those interested in helping analyze the data</li>\n<li>It's not too late to volunteer to help. Contact @mattfarina for more.</li>\n</ul>\n</li>\n</ul>\n<p>Watch the <a href=\"https://youtu.be/J-HWkEp8GcA\">recording</a>.</p>"},"site":{"siteMetadata":{"sigs":["sig-api-machinery","sig-apps","sig-architecture","sig-auth","sig-autoscaling","sig-aws","sig-azure","sig-big-data","sig-cli","sig-cloud-provider","sig-cluster-lifecycle","sig-cluster-ops","sig-contributor-experience","sig-docs","sig-gcp","sig-ibmcloud","sig-instrumentation","sig-multicluster","sig-network","sig-node","sig-openstack","sig-product-management","sig-release","sig-scalability","sig-scheduling","sig-service-catalog","sig-storage","sig-testing","sig-ui","sig-vmware","sig-windows"]}}},"pathContext":{"slug":"/sig-apps/minutes/2016-07-06/"}}

